if (typeof window !== 'undefined') { 
  const { Sigma, RandomizeNodePositions, RelativeSize } = require('react-sigma');
}


export default class GraphSurface extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResult: this.props.searchResult
    };
  }

  componentDidMount() {
    const { Sigma, RandomizeNodePositions, RelativeSize } = require('react-sigma');
  }

  render() {
    console.log("SearchObject", this.state.searchResult)
    if (typeof window !== 'undefined'){
      console.log("hallo")
      console.log('Sigma',Sigma)
    }

    let myGraph = { nodes: [{ id: "n1", label: "Alice" }, { id: "n2", label: "Rabbit" }], edges: [{ id: "e1", source: "n1", target: "n2", label: "SEES" }] };


    return (
      typeof window !== 'undefined' ? (
        <div>
          <div> SearchObject </div>
          <Sigma graph={myGraph} settings={{ drawEdges: true, clone: false }}>
            <RelativeSize initialSize={15} />
            <RandomizeNodePositions />
          </Sigma>
          <div> {this.state.searchResult.title} </div>
        </div>
      ) : null
    );
  }
}


